'use client';

import React, { useState } from 'react';
import { Heart, Plus, Calendar, Users } from 'lucide-react';

interface CellData {
    [key: string]: boolean;
}

interface RowData {
    regNo: string;
    other: string;
    cells: CellData;
}

const VolunteersReviewForm: React.FC = () => {
    // Get today's date in YYYY-MM-DD format
    const getTodaysDate = () => {
        const today = new Date();
        return today.toISOString().split('T')[0];
    };

    const [date, setDate] = useState<string>(getTodaysDate());
    const [team, setTeam] = useState<string>('');

    const columns = [
        'Vitals', 'Medicine', 'Catheter', 'Injection', 'Ryles Tube',
        'Dressing', 'PRE', 'Taping', 'IV Fluid', 'Specimen',
        'Bath', 'Consumable', 'Haircut'
    ];

    const [rows, setRows] = useState<RowData[]>(
        Array.from({ length: 1}, (_, index) => ({
            regNo: '',
            other: '',
            cells: columns.reduce((acc, col) => ({ ...acc, [col]: false }), {})
        }))
    );

    const handleCellToggle = (rowIndex: number, column: string) => {
        setRows(prev => prev.map((row, index) =>
            index === rowIndex
                ? { ...row, cells: { ...row.cells, [column]: !row.cells[column] } }
                : row
        ));
    };

    const handleRegNoChange = (rowIndex: number, value: string) => {
        setRows(prev => prev.map((row, index) =>
            index === rowIndex ? { ...row, regNo: value } : row
        ));
    };

    const handleOtherChange = (rowIndex: number, value: string) => {
        setRows(prev => prev.map((row, index) =>
            index === rowIndex ? { ...row, other: value } : row
        ));
    };

    const addRow = () => {
        setRows(prev => [...prev, {
            regNo: '',
            other: '',
            cells: columns.reduce((acc, col) => ({ ...acc, [col]: false }), {})
        }]);
    };

    const handleSubmit = (e: React.FormEvent) => {
        e.preventDefault();
        console.log('Form submitted:', { date, team, rows });
    };

    return (
        <div className="min-h-screen w-full bg-gradient-to-br from-blue-50 to-blue-100 px-2 sm:px-4 lg:px-8 py-4">
            <div className="max-w-full">
                {/* Full-width Header */}
                <div className="w-full bg-gradient-to-r from-blue-900 to-blue-700 text-white py-6 rounded-none mb-4">
                    <div className="flex justify-between items-start px-4 sm:px-6">
                        <div className="flex-1 text-center">
                            <div className="flex justify-center mb-3">
                                <Heart className="w-10 h-10 sm:w-12 sm:h-12 text-blue-200" />
                            </div>
                            <h1 className="text-2xl sm:text-3xl lg:text-4xl font-light mb-1">
                                Volunteer's Review
                            </h1>
                            <p className="text-sm sm:text-base lg:text-lg text-blue-200">
                                Daily Homecare Procedure Report
                            </p>
                        </div>

                    </div>
                </div>
                {/* Patient and Review IDs */}
                <div className="text-sm sm:text-base font-medium text-blue-900 mb-4 px-2 sm:px-4">
                    <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                        {/* Patient ID */}
                        <div className="flex justify-start">
                            <div className="flex items-center gap-2 bg-blue-50 text-blue-900 px-3 py-1.5 rounded-lg shadow-sm border border-blue-200 w-fit max-w-xs">
                                <span className="font-semibold">Patient ID:</span>
                            </div>
                        </div>

                        {/* Review ID */}
                        <div className="flex justify-start">
                            <div className="flex items-center gap-2 bg-blue-50 text-blue-900 px-3 py-1.5 rounded-lg shadow-sm border border-blue-200 w-fit max-w-xs">
                                <span className="font-semibold">Review ID:</span>
                            </div>
                        </div>
                    </div>
                </div>
                {/* Team Input */}
                <div className="bg-gray-50 p-3 sm:p-4 lg:p-5 rounded-xl border-l-4 border-blue-600 mb-6">
                    <div className="flex items-center mb-3">
                        <Users className="w-6 h-6 text-blue-600 mr-3" />
                        <h3 className="text-sm sm:text-base lg:text-lg font-semibold text-blue-900">
                            Team Information
                        </h3>
                    </div>

                    <div className="bg-white p-4 rounded-xl shadow-sm">
                        <div className="flex flex-col md:flex-row justify-between items-start gap-4">

                            {/* Team Input (left side) */}
                            <div className="w-full md:w-2/3">
                                <label className="block text-xs font-semibold text-blue-900 mb-2 uppercase tracking-wide">
                                    Team *
                                </label>
                                <input
                                    type="text"
                                    value={team}
                                    onChange={(e) => setTeam(e.target.value)}
                                    className="w-full p-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent transition-all duration-300 text-sm"
                                    required
                                />
                            </div>

                            {/* Date Picker (right side) */}
                            <label className="block text-xs font-semibold text-blue-900 mb-2 uppercase tracking-wide">
                                    Date *
                                </label><div className="flex items-center gap-2 bg-blue-50 text-blue-900 px-3 py-1.5 rounded-lg shadow-sm border border-blue-200 w-fit max-w-xs">
                                
                                <Calendar className="w-4 h-4 text-blue-600" />
                                <input
                                    type="date"
                                    value={date}
                                    onChange={(e) => setDate(e.target.value)}
                                    className="bg-transparent text-blue-900 text-sm focus:outline-none"
                                    required
                                />
                            </div>


                        </div>
                    </div>
                </div>


                {/* Table */}
                <div className="bg-white rounded-xl shadow-lg overflow-hidden">
                    <div className="overflow-x-auto">
                        <table className="w-full min-w-max">
                            <thead className="bg-gradient-to-r from-blue-900 to-blue-700 text-white">
                                <tr>
                                    <th className="px-3 py-4 text-left text-xs font-semibold uppercase tracking-wider min-w-24">
                                        Reg No
                                    </th>
                                    {columns.map((column) => (
                                        <th key={column} className="px-3 py-4 text-center text-xs font-semibold uppercase tracking-wider min-w-20">
                                            {column}
                                        </th>
                                    ))}
                                    <th className="px-3 py-4 text-left text-xs font-semibold uppercase tracking-wider min-w-32">
                                        Other (specify)
                                    </th>
                                </tr>
                            </thead>
                            <tbody className="bg-white divide-y divide-gray-200">
                                {rows.map((row, rowIndex) => (
                                    <tr key={rowIndex} className="hover:bg-blue-50 transition-colors duration-200">
                                        <td className="px-3 py-3">
                                            <input
                                                type="text"
                                                value={row.regNo}
                                                onChange={(e) => handleRegNoChange(rowIndex, e.target.value)}
                                                className="w-full p-2 border border-gray-300 rounded focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent text-sm"
                                                placeholder="Enter Reg No"
                                            />
                                        </td>
                                        {columns.map((column) => (
                                            <td key={column} className="px-3 py-3 text-center">
                                                <div
                                                    className="w-8 h-8 mx-auto border-2 border-gray-300 rounded cursor-pointer hover:border-blue-500 hover:bg-blue-50 transition-all duration-200 flex items-center justify-center"
                                                    onClick={() => handleCellToggle(rowIndex, column)}
                                                >
                                                    {row.cells[column] && (
                                                        <svg
                                                            className="w-5 h-5 text-green-600 font-bold"
                                                            fill="none"
                                                            stroke="currentColor"
                                                            viewBox="0 0 24 24"
                                                            xmlns="http://www.w3.org/2000/svg"
                                                        >
                                                            <path
                                                                strokeLinecap="round"
                                                                strokeLinejoin="round"
                                                                strokeWidth={3}
                                                                d="M5 13l4 4L19 7"
                                                            />
                                                        </svg>
                                                    )}
                                                </div>
                                            </td>
                                        ))}
                                        <td className="px-3 py-3">
                                            <input
                                                type="text"
                                                value={row.other}
                                                onChange={(e) => handleOtherChange(rowIndex, e.target.value)}
                                                className="w-full p-2 border border-gray-300 rounded focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent text-sm"
                                                placeholder="Specify other"
                                            />
                                        </td>
                                    </tr>
                                ))}
                                {/* Total Row */}
                                <tr className="bg-gray-50 font-semibold">
                                    <td className="px-3 py-3 text-blue-900">
                                        Total
                                    </td>
                                    {columns.map((column) => (
                                        <td key={column} className="px-3 py-3 text-center text-blue-900">
                                            {rows.reduce((sum, row) => sum + (row.cells[column] ? 1 : 0), 0)}
                                        </td>
                                    ))}
                                    <td className="px-3 py-3 text-blue-900">
                                        Summary
                                    </td>
                                </tr>
                            </tbody>
                        </table>
                    </div>
                </div>

                {/* Add Row Button */}
                <div className="flex justify-center mt-6">
                    <button
                        type="button"
                        onClick={addRow}
                        className="flex items-center gap-2 bg-blue-600 hover:bg-blue-700 text-white px-6 py-3 rounded-lg font-semibold transition-all duration-300 shadow-lg hover:shadow-xl"
                    >
                        <Plus className="w-5 h-5" />
                        Add Row
                    </button>
                </div>

                {/* Submit Button */}
                <div className="flex justify-center mt-8">
                    <button
                        onClick={handleSubmit}
                        className="bg-gradient-to-r from-blue-600 to-blue-700 hover:from-blue-700 hover:to-blue-800 text-white px-8 py-3 rounded-lg font-semibold transition-all duration-300 shadow-lg hover:shadow-xl"
                    >
                        Submit Report
                    </button>
                </div>
            </div>
        </div>
    );
};

export default VolunteersReviewForm;